import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, Separator, List, Search, FixedLayout, FormLayout, Cell, Switch } from '@vkontakte/vkui';
import './ListCode.css';

const ListCode = props => {	
				
const [checked, setChecked] = React.useState(false);

const toggleTrueFalse = () => setChecked(!checked);

	return (
	<Panel id={props.id}>
		<PanelHeader noShadow>
				
			Список 
		</PanelHeader>
	<FixedLayout vertical="top"><Search value={props.search} onChange={props.onChange}/></FixedLayout>
	

  {checked === false ? (  
  <>{props.numbers.length > 0 ? <>
    <FormLayout style={{ paddingTop: 60 }}>
		<Cell asideContent={<Switch checked={checked} onChange={toggleTrueFalse}/>}>
            Сортировать по алфавиту
          </Cell>
    </FormLayout>  
	<List>
                         
          {props.numbers.map((thematic, i, a, index) => (<div key={thematic.name}>
		  <div key={index} className="WrapperCell">
		  
	      <div key={thematic.name}>
		     {thematic.name}
		  </div>
		  
		  <div key={thematic.id} className="Test"> 
		     Код региона: {thematic.id}
		  </div>
		  
	  </div>
	  {i < a.length - 1 ? <Separator style={{ margin: '2px 0' }}/> : null}
	  </div> 
          ))}
     	 
	</List>
     </>: <List style={{ paddingTop: 60 }}><div style={{textAlign: "center", paddingTop: "10px", paddingBottom: "10px"}}>Ничего не найдено</div></List> }
	</>
   ) : ( 
   <>
   {props.regions.length > 0 ? <>
    <FormLayout style={{ paddingTop: 60 }}>
		<Cell asideContent={<Switch checked={checked} onChange={toggleTrueFalse}/>}>
            Сортировать по алфавиту
          </Cell>
    </FormLayout>  
    <List>
                         
          {props.regions.map((thematic, i, a, index) => (<div key={thematic.name}>
		  <div key={index} className="WrapperCell">
		  
	      <div key={thematic.name}>
		     {thematic.name}
		  </div>
		  
		  <div key={thematic.id} className="Test"> 
		     Код региона: {thematic.id}
		  </div>
		  
	  </div>
	  {i < a.length - 1 ? <Separator style={{ margin: '2px 0' }}/> : null}
	  </div> 
          ))}
     	 
	</List>
   </>: <List style={{ paddingTop: 60 }}><div style={{textAlign: "center", paddingTop: "10px", paddingBottom: "10px"}}>Ничего не найдено</div></List> }
	</>
   ) }
		
		
	</Panel>
	)
};


ListCode.propTypes = {
	id: PropTypes.string.isRequired,
};

export default ListCode;