class App extends React.Component {
	constructor(props) {
		super(props);
			
		this.state = {
			activePanel: 'home',
            activeStory: 'home',
            activeView: 'epe',
            value: '',	
            search: '',			
		};
		this.onStoryChange = this.onStoryChange.bind(this);
		this.addNumber = this.addNumber.bind(this);
		this.reset = this.reset.bind(this);
		this.onChange = this.onChange.bind(this);

	};

componentDidMount() {
		window.addEventListener('popstate', e => e.preventDefault() & this.menu(e));
		window.history.pushState( {Epic: 'epe' }, `epe` );
	}

onStoryChange (e) {
    this.setState({ activeStory: e.currentTarget.dataset.story })
  }
  

changeView = (e) => {
this.setState({ activeView: e.currentTarget.dataset.to })
window.history.pushState( { Epic: e.currentTarget.dataset.to }, `${e.currentTarget.dataset.to}`);
}	


 
menu (e) {
		if(e.state) {
			this.setState( { activeView: e.state.Epic } );
		} else {
			this.setState( {activeView: 'epe', search: '' } );
			connect.send("VKWebAppClose", {"status": "success"});
			window.history.pushState( { Epic: 'epe'}, `epe` );
		}
  }
  
  
addNumber = number => {
    return () => {
    this.setState({ value: (this.state.value + (number)).slice(0, 3) })
  };
  }; 
 
  
reset = () => {
    this.setState({ value: '' })
}	

removeLast = () => {
    this.setState({ value: this.state.value.substring(0, this.state.value.length - 1)})
  };
	
    onChange (search) { this.setState({ search }); }

    get numbers () {
      const search = this.state.search.toLowerCase().trim();
	  return parseInt(search) >=0 ? numbers.filter(({id}) => id.toLowerCase().indexOf(search) > -1) : numbers.filter(({name}) => name.toLowerCase().indexOf(search) > -1) ;
    }

    get regions () {
      const search = this.state.search.toLowerCase().trim();
      return parseInt(search) >=0 ? regions.filter(({id}) => id.toLowerCase().indexOf(search) > -1) : regions.filter(({name}) => name.toLowerCase().indexOf(search) > -1) ;
    }
	
	render() {
		return (
		<Root activeView={this.state.activeView}>
		    <Epic id="epe" activeStory={this.state.activeStory} tabbar={
                  <Tabbar>
                     <TabbarItem
                       onClick={this.onStoryChange}
                       selected={this.state.activeStory === 'home'}
                       data-story="home"
                       text="Поиск"
                     ><Icon28SearchOutline/></TabbarItem>
                     <TabbarItem
                      onClick={this.onStoryChange}
                      selected={this.state.activeStory === 'ListCode'}
                      data-story="ListCode"
                      text="Список"
                     ><Icon28ArticleOutline/></TabbarItem>
                  </Tabbar>
                   }>
			      <View id="home" activePanel={this.state.activePanel}>
				      <Home id="home" changeView={this.changeView} value={this.state.value} addNumber={this.addNumber} reset={this.reset} removeLast={this.removeLast} />
			      </View>
				   <View id="ListCode" activePanel="ListCode">
				      <ListCode id="ListCode" changeView={this.changeView} onChange={this.onChange} search={this.state.search} numbers={this.numbers} regions={this.regions}/>
			      </View>
		    </Epic>
		          <View id="Maps"  activePanel="Maps">
			          <Maps id="Maps" value={this.state.value} />
			      </View>
	    </Root>
		);
	}
}

export default App;
