import React from 'react';
import { Panel, PanelHeader } from '@vkontakte/vkui';
import ComponentResult from './ComponentResult';
import './stylesHome.css';
import Icon28Place from '@vkontakte/icons/dist/28/place';

const messages = [

  
  {
    values: [ '00',['010','019'],['020','029'],['030','039'],['040','049'],['050','059'],['060','069'],['070','079'],['080','089'],['090','099'],['000','009'],'100',[200,715],[717,724],[726,749],[751,760],'762',[764,776],[778,789],[791,796],'798',[800,999] ],
    message: 'Код не существует',
  },
  
];

const numbers123 = [1,2,3]
const numbers456 = [4,5,6]
const numbers789 = [7,8,9]

function Home(props) {
  

  return (
  <Panel id={props.id}>
		<PanelHeader>Поиск по коду</PanelHeader>

		<div>
		<ComponentResult id="ComponentResult" value={props.value} />
		
                  {messages
                     .filter(n => n.values.some(v => (
                      (v instanceof Array && v[0] <= props.value && props.value <= v[1]) ||
                        v === props.value
                       )))
                     .map(n => <div className="region-code" key>{n.message}</div>)
                  }
        </div>
		
		<div className="ONLYBOXSHADOW"></div>
		 <button className="GoToMapOff"><Icon28Place className="buttonMapOff"/></button>
		{(props.value) > '1'&& props.value <=99 ? (<button className="GoToMapOn" onClick={  props.changeView }  data-to="Maps"><Icon28Place className="buttonMapOn"/></button>): null }
		{(props.value) > 100&& props.value <=199 ? (<button className="GoToMapOn" onClick={  props.changeView }  data-to="Maps"><Icon28Place className="buttonMapOn"/></button>): null }
		{['1','01','02','03','04','05','06','07','08','09','716','750','790','761','763','777','797','799','725'].includes(props.value) ? ( <button className="GoToMapOn" onClick={  props.changeView }  data-to="Maps"><Icon28Place className="buttonMapOn"/></button>) : null }
           		
		   <div className="wrapper-input-wrapper">
               <div className="input-wrapper">
                 <input className="Input" value={props.value} readOnly={true}/>
               </div>
		   </div>
			
			
			
    <div className="wrapper-buttons" >
         <div>
                {numbers123.map(number => (
                 <button className="buttons-keyboard" key={number} onClick={props.addNumber(number)}>
                    {number}
                 </button>
				 ))}
                  
				 <br/>
				 {numbers456.map(number => (
				 <button className="buttons-keyboard" key={number} onClick={props.addNumber(number)}>
                   {number}
                 </button>
				 ))}
            
				 <br/>
				 {numbers789.map(number => (
				  <button className="buttons-keyboard" key={number} onClick={props.addNumber(number)}>
                    {number}
                 </button>
				  ))}
				 
                 
		      <br/>
			     
		         <button className="buttons-keyboard" onClick={props.addNumber(0)}>0</button>
		         <button className="buttons-keyboard" onClick={props.removeLast} style={{ color: "red", display: "none" }}><img src="https://i.ibb.co/xStC0pX/delete.png" alt="delete" border="0"/></button>
		         <button className="buttons-keyboard" onClick={props.reset} style={{ color: "red" }}>C</button>
        </div>
	</div>



	</Panel>
  );
}

export default Home;